/***----------- Google Fonts -----------***/

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

/***---------- Configurations ----------***/

:root {
    --font-main: "Poppins", sans-serif;
    --color-1: #7857fe;
    --color-2: #f5ae10;
    --color-3: #e91e63;
    --color-100: #ffffff;
    --color-200: #f7f7f7;
    --color-300: #666666;
    --color-400: #555555;
    --color-500: #cecece;
    --color-700: #1c1d21;
    --color-900: #000000;
    --color-dark: #2b2c2f;
    --color-main: #f05c28;
    --shadow-100: 0 5px 15px rgba(0, 0, 0, 0.1);
    --shadow-200: 0 5px 15px rgba(0, 0, 0, 0.3);
    --outer-shadow: 0px 0px 18px 0 #0000002c;
    --color-alpha: rgba(0, 0, 0, 0.05);
    --color-main-dark: var(--color-main);
    --color-dark-main: var(--color-dark);
}

/***---------- Global Style ------------***/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none !important;
}

body {
    font-family: var(--font-main);
    font-size: 16px;
    background-color: var(--color-100);
    overflow-x: hidden;
    line-height: 1.5;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

a,
a:hover {
    text-decoration: none !important;
}

p {
    font-size: 15px;
    line-height: 26px;
    margin: 0;
    font-weight: 400;
    color: var(--color-300);
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

.bg-img {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.section-padding {
    padding: 80px 0;
}

/***--------------- Dark ---------------***/

body.dark {
    background: var(--color-dark);
    --color-900: #ffffff;
    --color-400: #bbbbbb;
    --color-200: var(--color-dark);
    --shadow-100: var(--shadow-200);
    --color-alpha: rgba(255, 255, 255, 0.05);
    --color-main-dark: var(--color-dark);
    --color-dark-main: var(--color-main);
}

/***--------------- Title --------------***/

.section-title {
    margin-bottom: 60px;
}

.section-title h2 {
    font-size: 40px;
    color: var(--color-900);
    font-weight: 700;
    text-align: center;
    text-transform: capitalize;
}

.section-title h2 span {
    color: var(--color-main);
}

/***-------------- Buttons -------------***/

.btn-main {
    padding: 12px 30px;
    border: none;
    border-radius: 30px;
    text-transform: capitalize;
    transition: all 0.3s ease;
    font-weight: 500;
    color: #f05c28;
    background-color: var(--color-100);
    box-shadow: var(--shadow-200);
}

.btn-cta:focus,
.btn-main:focus {
    box-shadow: var(--shadow-200);
}

.btn-main:hover {
    color: var(--color-100);
    background-color: var(--color-main);
}

.btn-cta {
    padding: 12px 30px;
    border: none;
    border-radius: 30px;
    text-transform: capitalize;
    transition: all 0.3s ease;
    font-weight: 500;
    background-color: var(--color-main);
    color: var(--color-100);
    box-shadow: var(--shadow-200);
}

.btn-cta:hover {
    color: var(--color-main);
    background-color: var(--color-100);
}

/***------------ OwlCarousel -----------***/

.owl-carousel .owl-dots {
    padding: 0 15px;
    text-align: center;
    margin-top: 20px;
}

.owl-carousel button.owl-dot {
    background-color: #dddddd;
    height: 6px;
    width: 24px;
    display: inline-block;
    margin: 0 4px;
    border-radius: 5px;
}

.owl-carousel button.owl-dot.active {
    background-color: var(--color-main);
}

/***------------- Keyframes ------------***/

@keyframes spinOne {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes spinTwo {
    0% {
        transform: translateY(0px) translateX(0) rotate(0) scale(1);
        opacity: 0.8;
    }
    25% {
        transform: translateY(-10px) translateX(-10px) rotate(20deg) scale(0.8);
        opacity: 0.9;
    }
    50% {
        transform: translateY(-15px) translateX(-15px) rotate(10deg) scale(0.9);
        opacity: 0.8;
    }
    75% {
        transform: translateY(-20px) translateX(-20px) rotate(20deg) scale(0.75);
        opacity: 0.6;
    }
    85% {
        transform: translateY(-25px) translateX(-25px) rotate(20deg) scale(0.9);
        opacity: 0.7;
    }
    100% {
        transform: translateY(-15px) translateX(-15px) rotate(0) scale(0.95);
        opacity: 0.85;
    }
}

@keyframes spinTree {
    0% {
        transform: translateY(10px) translateX(0) rotate(0) scale(1);
        opacity: 0.9;
    }
    20% {
        transform: translateY(20px) translateX(10px) rotate(-20deg) scale(0.8);
        opacity: 0.8;
    }
    40% {
        transform: translateY(15px) translateX(-15px) rotate(10deg) scale(0.75);
        opacity: 0.8;
    }
    40% {
        transform: translateY(-15px) translateX(-25px) rotate(10deg) scale(0.5);
        opacity: 1;
    }
    80% {
        transform: translateY(-30px) translateX(20px) rotate(-20deg) scale(0.75);
        opacity: 0.6;
    }
    100% {
        transform: translateY(15px) translateX(15px) rotate(0) scale(0.95);
        opacity: 0.7;
    }
}

@keyframes bounceTopOne {
    0%,
    100% {
        transform: translateY(-30px);
    }
    50% {
        transform: translateY(0px);
    }
}

@keyframes pulseOne {
    0% {
        transform: scale(0.94);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
    }
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 12px rgba(255, 255, 255, 0);
    }
    100% {
        transform: scale(0.94);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    }
}

@keyframes zoomInOut {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}
